import {
    Input, Col, Row, Select, InputNumber, DatePicker, AutoComplete, Cascader, Tooltip, Icon, Radio, Divider
  } from 'antd';
import React, { Component } from 'react';

import './Personal.css'
const InputGroup = Input.Group;
const Option = Select.Option;

const { MonthPicker, RangePicker, WeekPicker } = DatePicker;

class PersonalInfo extends Component {
  state = {
    cinsiyet : "erkek",
    level : "lisans",
    uzmanlıkalani : "",
    uzmanlıkyili : "",
    mezuniyetyili : "",
    kurum : ""
  }
  handleChangeLevel = (value) => {
    this.setState({
      level : value
    })
  }

  handleChangeCinsiyet = (value) => {
    let st = this.state
    st.cinsiyet = value.target.value;
    this.setState({
      cinsiyet : value.target.value
    })
    this.props.changeInfo(st)
  }
  handleChangeuzmanlıkalanı= (value) => {
    let st = this.state
    st.uzmanlıkalani = value;
    this.setState({uzmanlıkalani: value})
    this.props.changeInfo(st)
  }
  handleChangeuzmanlıkyili= (value,str) => {
    let st = this.state
    st.uzmanlıkyili = str;
    this.setState({uzmanlıkyili: str})
    this.props.changeInfo(st)
  }
  handleChangemezuniyetYılı = (value,str) => {
    let st = this.state
    st.mezuniyetyili = str;
    this.setState({mezuniyetyili : str})
    this.props.changeInfo(st)
  } 
  handleChangekurum = (value) => {
    let st = this.state
    st.kurum = value;
    this.setState({kurum: value})
    this.props.changeInfo(st)
  }
  render() {
    
    return (
      <div>
        <Divider orientation="left"> Kişisel Bilgiler</Divider>
        <table >
          <tbody>
            <tr>
              <td>
                Cinsiyet
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Radio.Group value={this.state.cinsiyet} onChange={this.handleChangeCinsiyet}>
                    <Radio value="erkek">Erkek</Radio>
                    <Radio value="kadın">Kadın</Radio>
                  </Radio.Group>
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Mezuniyet Yılı
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <MonthPicker onChange={this.handleChangemezuniyetYılı} style={{width:300}}/>
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Eğitim Durumu
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Select placeholder="Öğrenim durumunu seçiniz" style={{width:300}} onChange={this.handleChangeLevel}>
                    <Select.Option key="lisans">Lisans</Select.Option>
                    <Select.Option key="uögrenci">Uzmanlık Öğrencisi</Select.Option>
                    <Select.Option key="uzman">Uzman</Select.Option>
                  </Select>
                  {this.state.level === "uzman" ? <MonthPicker onChange={this.handleChangeuzmanlıkyili} placeholder="Uzmanlık Yılını Giriniz" style={{width:200}}/> : null}
                  {this.state.level === "uzman" || this.state.level === "uögrenci" ?  
                    <Select onChange={this.handleChangeuzmanlıkalanı} placeholder="Uzmanlık Alanınızı seçiniz" style={{width:300}} >
                      <Select.Option key="Ağız Diş ve Çene Cerrahisi">Ağız Diş ve Çene Cerrahisi</Select.Option>
                      <Select.Option key="Ağız Diş ve Çene Radyolojisi">Ağız Diş ve Çene Radyolojisi</Select.Option>
                      <Select.Option key="Çocuk Diş Hekimliği">Çocuk Diş Hekimliği</Select.Option>
                      <Select.Option key="Endodonti">Endodonti</Select.Option>
                      <Select.Option key="Ortodonti">Ortodonti</Select.Option>
                      <Select.Option key="Periodontoloji">Periodontoloji</Select.Option>
                      <Select.Option key="Restoratif Diş Tedavisi">Restoratif Diş Tedavisi</Select.Option>
                      <Select.Option key="Protetik Diş Tedavisi">Protetik Diş Tedavisi</Select.Option>
                    </Select> 
                  : null}
                </InputGroup>
              </td>
            </tr>
            <tr >
              <td>
                Çalıştığı Kurum
              </td>
              <td>
                :
              </td>
              <td>
                <InputGroup compact style={{margin : 10}}>
                  <Select onChange={this.handleChangekurum} placeholder="Çalışma Durumunu Seçiniz" style={{width:300}}>
                    <Select.Option key="Üniversite">Üniversite</Select.Option>
                    <Select.Option key="Özel Poliklinik">Özel Poliklinik</Select.Option>
                    <Select.Option key="Özel Muayenehane">Özel Muayenehane</Select.Option>
                    <Select.Option key="Devlet Hastanesi">Devlet Hastanesi</Select.Option>
                    <Select.Option key="Ağız ve Diş Sağlığı Merkezi-Hastanesi">Ağız ve Diş Sağlığı Merkezi-Hastanesi</Select.Option>
                  </Select>
                </InputGroup>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default PersonalInfo;