import {
  Modal,  Button, Row, Col, message
  } from 'antd';
import React, { Component } from 'react';
import Teeth from './Teeth';
import { Stage, Layer,Image } from 'react-konva';
import useImage from 'use-image';

  class Question1 extends Component {
    state = {
      dataSource: [],
      application: undefined,
      answer : [],
      leftTop : [],
      rightTop : [],
      rightBottom : [],
      leftBottom : [],
      infos : this.props.infos
    }


    componentDidMount(){
      var leftTop = []
      for (let index = 8; index >= 1; index--) {
        leftTop.push({isActive : true, info : [], application : []})
      }
      var rightTop = []
      for (let index = 1; index <= 8; index++) {
        rightTop.push({isActive : true, info : [],  application : []})
      }
      var rightBottom  = []
      for (let index = 1; index <= 8; index++) {
        rightBottom.push({isActive : true, info : [], application : []})
      }
      var leftBottom  = []
      for (let index = 8; index >= 1; index--) {
        leftBottom.push({isActive : true, info : [], application : []})
      }
      for (let index = 0; index < this.state.infos.length; index++) {
        if(this.state.infos){
          if(this.state.infos[index][0] === 4){
            leftBottom[8-this.state.infos[index][1]].info.push(this.state.infos[index][2])
          }
        }
      }
      this.setState({leftTop: leftTop})
      this.setState({rightTop: rightTop})
      this.setState({rightBottom: rightBottom})
      this.setState({leftBottom: leftBottom})
    }
    
    clickTeeth = (index,loc) => {
      if (!this.state.application){
        message.error("Lütfen Bir İşlem Seçiniz")
        return
      }
      let answer = this.state.answer
      let leftTop = this.state.leftTop
      let rightTop = this.state.rightTop
      let rightBottom = this.state.rightBottom
      let leftBottom = this.state.leftBottom
      for (let i = 0; i < answer.length; i++) {
        if(index === answer[i][0] && loc === answer[i][1] && this.state.application === answer[i][2]){
          message.info(this.state.application + " işlemi " + loc + index + " nolu dişten kaldırıldı" )
          this.setState({application : undefined})
          answer.splice(i,1)
          if(loc === 1){
            for (let t = 0; t < leftTop[8-index].application.length; t++) {
              if(leftTop[8-index].application[t] == this.state.application){
                leftTop[8-index].application.splice(t,1)
                this.setState({leftTop : leftTop})
                return
              }
            }
          }else if(loc === 2){
            for (let t = 0; t < rightTop[8-index].application.length; t++) {
              if(rightTop[8-index].application[t] == this.state.application){
                rightTop[8-index].application.splice(t,1)
                this.setState({rightTop : rightTop})
                return
              }
            }
          }else if(loc === 3){
            for (let t = 0; t < rightBottom[8-index].application.length; t++) {
              if(rightBottom[8-index].application[t] == this.state.application){
                rightBottom[8-index].application.splice(t,1)
                this.setState({rightBottom : rightBottom})
                return
              }
            }
          }else if(loc === 4){
            for (let t = 0; t < leftBottom[8-index].application.length; t++) {
              if(leftBottom[8-index].application[t] == this.state.application){
                leftBottom[8-index].application.splice(t,1)
                this.setState({leftBottom : leftBottom})
                return
              }
            }
          }
          return
        }
      }
      if(loc === 1){
        leftTop[8-index].application.push(this.state.application)
        this.setState({leftTop : leftTop})
      }else if(loc === 2){
        rightTop[index-1].application.push(this.state.application)
        this.setState({rightTop : rightTop})
      }else if(loc === 3){
        rightBottom[index-1].application.push(this.state.application)
        this.setState({rightBottom : rightBottom})
      }else if(loc === 4){
        leftBottom[8-index].application.push(this.state.application)
        this.setState({leftBottom : leftBottom})
      }
      answer.push([index,loc,this.state.application])
      this.setState({answer : answer})
      this.setState({application : undefined})
      message.info(this.state.application + " işlemi " + loc +   index+ " nolu dişe uygulandı" )
      this.props.changeAnswer(this.props.id,answer)
    }
    
    selectApplication = (state) => {
      if(state === "Parsiyel Protez"){
        let cekim = false;
        for (let index = 0; index < this.state.leftBottom[0].application.length; index++) {
          let element = this.state.leftBottom[0].application[index];
          if(element === "Çekim"){
            cekim = true
          }
        }
        if(cekim){
          this.setState({application : "Parsiyel Protez 2"})
        }else{
          this.setState({application : "Parsiyel Protez 1"})
        }
      }else{
        this.setState({application : state})
      }
      message.info("Seçili İşlem : " + state)
    }


    cretaBottomTeeth = () => {
      let teeth = []
      
      for (let index = 1; index <= 16; index++) {
        teeth.push(<div key={index}><Button>{index}</Button></div> )
      }
      return teeth
    }

    isActive = (type) => {
      if(this.state.application){
        if(type === this.state.application){
          return true
        }
      }
      return false
    }
    render() {
      return (
      <div>
          {this.state.leftTop.map((object,i) => <Teeth click={this.clickTeeth}  index={8-i} loc={1} obj={object}/>)}
          {this.state.rightTop.map((object,i) => <Teeth click={this.clickTeeth} index={i+1} loc={2} obj={object}/>)}
          <br/>
          <br/>
          {this.state.leftBottom.map((object,i) => <Teeth click={this.clickTeeth} index={8-i} loc={4} obj={object}/>)}
          {this.state.rightBottom.map((object,i) => <Teeth click={this.clickTeeth} index={i+1} loc={3} obj={object}/>)}
          <Row gutter={16} style={{margin: '10px'}}>
            Tedavi Türleri
          </Row>
          <Row gutter={16} style={{margin: '10px'}}>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Köprü")} onClick={() => this.selectApplication("Köprü")}>Köprü</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("İmplant")} onClick={() => this.selectApplication("İmplant")}>İmplant</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Parsiyel Protez")} onClick={() => this.selectApplication("Parsiyel Protez")}>Parsiyel Protez (2 Diş)</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Çekim")} onClick={() => this.selectApplication("Çekim")}>Çekim</Button>
            </Col>
          </Row>
          <Row gutter={16} style={{margin: '10px'}}>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Dolgu (Komposit)")} onClick={() => this.selectApplication("Dolgu (Komposit)")}>Dolgu (Komposit)</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Dolgu (Amalgam)")} onClick={() => this.selectApplication("Dolgu (Amalgam)")}>Dolgu (Amalgam)</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Kanal Tedavisi")} onClick={() => this.selectApplication("Kanal Tedavisi")}>Kanal Tedavisi</Button>
            </Col>
            <Col span={6}>
              <Button type="primary" block disabled={this.isActive("Kron")} onClick={() => this.selectApplication("Kron")}>Kron</Button>
            </Col>
          </Row>
          <Row gutter={16} style={{margin: '10px'}}>
           
          </Row>
          
      </div>
      );
    }
  }

  export default Question1;