
import React, { Component } from 'react';
import { Button, Popover } from 'antd';
import {Image, Text, StyleSheet} from 'react';

class Teeth extends Component {
  state = {
    index : this.props.index,
    loc : this.props.loc,
    clicked : false,
  };

  convertIntoContent = (info,application) => {

    let ret = [<div><b>Diş Durumu</b></div>]
    if(info.length > 0){
      for (let index = 0; index < info.length; index++) {
        ret.push(<div>{info[index]}</div>)
      }
    }else{
      ret.push([<div>Sağlıklı Diş</div>])
    }
    ret.push([<div><br/><b>Uygulanan İşlemler</b></div>])
    if(application.length > 0){
      for (let index = 0; index < application.length; index++) {
        ret.push(<div>{application[index]}</div>)
      }
    }else{
      ret.push([<div>Yok</div>])
    }
    return ret
  }

  changeType = (info,application,op) => {
    if(op !== "natural"){
      return 1
    }
    if(info.length > 0){
      for (let index = 0; index < info.length; index++) {
        if(info[index]=== "Diş Yok"){
          return 0
        }
      }
    }
    if(application.length > 0){
      for (let index = 0; index < application.length; index++) {
        if(application[index]=== "Çekim"){
          return 0
        }
      }
    }
    else{
      return 1
    }
  }

  getImages(application,loc,index){
    return "./images/disler/"+loc + index+"_dis.png"
  }

  getInfoImages(info,loc,index){
    let images = []
    if(info.length > 0){
      for (let i = 0; i < info.length; i++) {
        if(info[i ]=== "Yüzeysel Çürük"){
          images.push(<img width={20} onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/cürük/yuzeysel_curuk_48.png"} style={{marginLeft:-40,marginRight:20,marginTop:-75, 
            opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
        }
        else if(info[i ]=== "Derin Çürük"){
          images.push(<img width={30} onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/cürük/derin_curuk_48_yeni.png"} style={{marginLeft:-48,marginRight:14,marginTop:-68,
            opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
        }
        else if(info[i ]=== "Kötü Kanal Tedavisi"){
          images.push(<img width={35} onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/kanal_tedavisi/hatali_kanal_tedavi_48.png"} style={{marginLeft:-50,marginRight:15,marginTop:-10,
            opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
        }
        else if(info[i ]=== "İyi Kanal Tedavisi"){
          images.push(<img width={35} onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/kanal_tedavisi/48_kanal_tedavi.png"} style={{marginLeft:-52,marginRight:17,marginTop:13,
            opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
        }

      }
      if(info.length > 0){
        for (let i = 0; i < info.length; i++) {
          if(info[i ]=== "Lezyon Küçük"){
            images.push(<img onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/lezyon/lezyon_kucuk.png"} style={{marginLeft:-63,marginRight:45,marginTop:83,
              opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
          }
          else if(info[i ]=== "Lezyon Büyük"){
            images.push(<img onClick={ () => this.props.click(this.state.index,this.state.loc)} src={"./images/lezyon/lezyon_buyuk.png"} style={{marginLeft:-45,marginRight:25,marginTop:83,
              opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}/>)
          }
        }
      }
      return images
    }else{
      return ""
    }
  }

  getApplicationSize(info,loc,index){
    if(info === "Kanal Tedavisi"){
        if(loc === 4){
          if(index === 8){
            return 35
          }
          if(index === 7){
            return 37
          }
          if(index === 6){
            return 42
          }
          if(index === 5){
            return 13
          }
          if(index === 4){
            return 13
          }
        }
    }else if(info === "Köprü"){
      if(loc === 4){
        if(index === 8){
          return 65
        }
        if(index === 7){
          return 55
        }
        if(index === 6){
          return 55
        }
        if(index === 5){
          return 35
        }
        if(index === 4){
          return 35
        }
      }
    }else if(info === "Kron"){
      if(loc === 4){
        if(index === 8){
          return 65
        }
        if(index === 7){
          return 55
        }
        if(index === 6){
          return 55
        }
        if(index === 5){
          return 35
        }
        if(index === 4){
          return 35
        }
      }
    }else if(info === "İmplant"){
      if(loc === 4){
        if(index === 8){
          return 20
        }
        if(index === 7){
          return 20
        }
        if(index === 6){
          return 20
        }
        if(index === 5){
          return 20
        }
        if(index === 4){
          return 20
        }
      }
    }else if(info === "Dolgu (Amalgam)"){
      if(loc === 4){
        if(index === 8){
          return 50
        }
        if(index === 7){
          return 0
        }
        if(index === 6){
          return 0
        }
        if(index === 5){
          return 0
        }
        if(index === 4){
          return 0
        }
      }
    }else if(info === "Dolgu (Komposit)"){
      if(loc === 4){
        if(index === 8){
          return 40
        }
        if(index === 7){
          return 0
        }
        if(index === 6){
          return 0
        }
        if(index === 5){
          return 0
        }
        if(index === 4){
          return 0
        }
      }
    }else if(info === "Parsiyel Protez 1"){
      if(loc === 4){
        if(index === 8){
          return 0
        }
        if(index === 7){
          return 0
        }
        if(index === 6){
          return 175
        }
        if(index === 5){
          return 0
        }
        if(index === 4){
          return 0
        }
      }
    }else if(info === "Parsiyel Protez 2"){
      if(loc === 4){
        if(index === 8){
          return 0
        }
        if(index === 7){
          return 0
        }
        if(index === 6){
          return 200
        }
        if(index === 5){
          return 0
        }
        if(index === 4){
          return 0
        }
      }
    }
  }

  getApplicationMargin(info,loc,index){
    let margins = []
    if(info === "Kanal Tedavisi"){
        if(loc === 4){
          if(index === 8){
            margins.push(-52)
            margins.push(17)
            margins.push(13)
          }
          if(index === 7){
            margins.push(-50)
            margins.push(13)
            margins.push(25)
          }
          if(index === 6){
            margins.push(-55)
            margins.push(13)
            margins.push(25)
          }
          if(index === 5){
            margins.push(-25)
            margins.push(12)
            margins.push(22)
          }
          if(index === 4){
            margins.push(-23)
            margins.push(10)
            margins.push(24)
          }
        }
    }else if(info === "Köprü"){
      if(loc === 4){
        if(index === 8){
          margins.push(-65)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 7){
          margins.push(-55)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 6){
          margins.push(-55)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 5){
          margins.push(-35)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 4){
          margins.push(-35)
          margins.push(0)
          margins.push(-60)
        }
      }
    }else if(info === "Kron"){
      if(loc === 4){
        if(index === 8){
          margins.push(-65)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 7){
          margins.push(-55)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 6){
          margins.push(-55)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 5){
          margins.push(-35)
          margins.push(0)
          margins.push(-60)
        }
        if(index === 4){
          margins.push(-35)
          margins.push(0)
          margins.push(-60)
        }
      }
    }else if(info === "İmplant"){
      if(loc === 4){
        if(index === 8){
          margins.push(-45)
          margins.push(25)
          margins.push(20)
        }
        if(index === 7){
          margins.push(-35)
          margins.push(15)
          margins.push(25)
        }
        if(index === 6){
          margins.push(-35)
          margins.push(15)
          margins.push(20)
        }
        if(index === 5){
          margins.push(-28)
          margins.push(8)
          margins.push(20)
        }
        if(index === 4){
          margins.push(-27)
          margins.push(7)
          margins.push(25)
        }
      }
    }else if(info === "Dolgu (Amalgam)"){
      if(loc === 4){
        if(index === 8){
          margins.push(-60)
          margins.push(10)
          margins.push(-75)
        }
      }
    }else if(info === "Dolgu (Komposit)"){
      if(loc === 4){
        if(index === 8){
          margins.push(-55)
          margins.push(15)
          margins.push(-55)
        }
      }
    }
    else if(info === "Parsiyel Protez 1"){
        if(loc === 4){
          if(index === 6){
            margins.push(-150)
            margins.push(-25)
            margins.push(0)
          }
        }
    }
    else if(info === "Parsiyel Protez 2"){
      if(loc === 4){
        if(index === 6){
          margins.push(-175)
          margins.push(-25)
          margins.push(0)
        }
      }
  }
    return margins
  }

  
  getApplicationImages(info,loc,index){
    let images = []
    if(info.length > 0){
      for (let i = 0; i < info.length; i++) {
        if(info[i ]=== "İmplant"){
          let margins = this.getApplicationMargin(info[i],loc,index)
          images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/implant_protez/implant.png"}
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
      }
    }
    if(info.length > 0){
      for (let i = 0; i < info.length; i++) {
        if(info[i ]=== "Kanal Tedavisi"){ 
          let margins = this.getApplicationMargin(info[i],loc,index)
          images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/kanal_tedavisi/"+loc +index+"_kanal_tedavi.png"} 
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
        if(info[i ]=== "Köprü"){
          let margins = this.getApplicationMargin(info[i],loc,index)
          images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/köprü/"+loc +index+"_köprü.png"}
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
        if(info[i ]=== "Kron" || info[i] === "İmplant"){
          let margins = this.getApplicationMargin("Kron",loc,index)
          images.push(<img width={this.getApplicationSize("Kron",loc,index)} src={"./images/kron/"+loc +index+"_kron.png"}
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
        if(info[i ]=== "Dolgu (Komposit)"){
          let margins = this.getApplicationMargin(info[i],loc,index)
          images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/amalgam/amalgam_48.png"}
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
        if(info[i ]=== "Dolgu (Amalgam)"){
          let margins = this.getApplicationMargin(info[i],loc,index)
          images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/amalgam/komposit.png"}
          style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
          onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
        }
      }
      if(info.length > 0){
        for (let i = 0; i < info.length; i++) {
          if(info[i ]=== "Parsiyel Protez 1"){
            let margins = this.getApplicationMargin(info[i],loc,index)
            images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/implant_protez/protez_47-46-45.png"}
            style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
            onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
          }
          if(info[i ]=== "Parsiyel Protez 2"){
            let margins = this.getApplicationMargin(info[i],loc,index)
            images.push(<img width={this.getApplicationSize(info[i],loc,index)} src={"./images/implant_protez/protez_48-47-46-45.png"}
            style={{marginLeft:margins[0],marginRight:margins[1],marginTop:margins[2],opacity:this.changeType(this.props.obj.info,this.props.obj.application,"not_natural")}}
            onClick={ () => this.props.click(this.state.index,this.state.loc)}/>)
          }
        }
      }
      return images
    }else{
      return ""
    }
  }
  

  

  render() {
    return(
      <Popover content={this.convertIntoContent(this.props.obj.info,this.props.obj.application)} title={""+ this.props.loc +this.state.index}>
        <img height={100} style={{opacity:this.changeType(this.props.obj.info,this.props.obj.application,"natural")}} onClick={ () => this.props.click(this.state.index,this.state.loc)} 
        src={this.getImages(this.props.obj.application,this.props.loc,this.state.index)}></img>
        {this.getInfoImages(this.props.obj.info,this.props.loc,this.state.index)}
        {this.getApplicationImages(this.props.obj.application,this.props.loc,this.state.index)}
      </Popover>
    );
  }
}


export default Teeth;